.pagination{
  text-align: center;
  margin: 70px 0 40px 0 ;

  li {
      display: inline-block;
  }

  a {
      align-items: center;
      height: 40px;
      padding: 10px;
      border: 1px solid #666;
      border-radius: 5px;
      margin-right: 10px;
      cursor: pointer;

      &:hover {
          background-color: #737272;
          color: white;   
      }
  }

  :global {
      .selected {
          a {
              background-color: #737272;
          }
      }
  }
}