@import './variables';
@import './libs/normalize';
@import './components/all';

body {
  background-color: $background;
  overflow-y:scroll;
}

.wrapper {
  width: calc(100vw - 100px);
  height: 100%;
  background-color: #fff;
  margin: 50px auto;
  border-radius: 10px;
  max-width: 1400px;

  @media (max-width: 645px) {
    width: calc(100vw - 30px);
    margin-top: 15px;
  }
}

.content {
  padding: 40px 0;

  &__error-info {
    margin: 100px auto;
    width: 500px;
    text-align: center;

    h2 {
      font-size: 32px;
      font-weight: 700;
      margin-bottom: 10px;
    }

    p{
      font-size: 20px;
    }
  }

  &__title {
    font-weight: 800;
    font-size: 38px;
    margin: 60px 0 50px 0;
  }

  &__items {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    grid-template-rows: repeat(2, 1fr);
    grid-column-gap: 50px;
    grid-row-gap: 60px;

    @media (max-width: 1450px) {
      grid-template-columns: repeat(3, 1fr);
    }

    @media (max-width: 1100px) {
      grid-template-columns: repeat(2, 1fr);
    }

    @media (max-width: 750px) {
      grid-template-columns: repeat(1, 1fr);
    }

  }

  &__top {
    display: flex;
    align-items: center;
    justify-content: space-between;

    @media (max-width: 1260px) {

      .categories {
        width: 100%;
        overflow:auto;
        
        ul {
          width: 785px;
        }
      }

        .sort {
          padding-left: 30px;
          b{
            display: none;
          }
        }
    }

    @media (max-width: 820px) {
      .categories {
        margin-bottom: 30px;
      }
      flex-direction: column;
    }

    @media (max-width: 760px) {
      .categories {
        ul{
          li{
            padding: 8px 20px;
          }
        }
      }
    }
  }
  
}

.container {
  width: $container-width;
  margin: 0 auto;

  &--cart {
    max-width: 820px;
    margin: 90px auto;
    .content__title {
      margin: 0;
    }
  }
}

.cart {
  &__top {
    display: flex;
    justify-content: space-between;
    align-items: center;

    @media (max-width: 440px) {
      flex-direction: column;

      .content__title{
        margin-bottom: 30px;
      }
    } 
  }

  .content__title {
    display: flex;
    align-items: center;
    font-size: 32px;

    svg {
      position: relative;
      top: -2px;
      width: 30px;
      height: 30px;
      margin-right: 10px;
      path {
        stroke: $black;
        stroke-width: 1.9;
      }
    }
  }

  &__clear {
    display: flex;
    align-items: center;
    cursor: pointer;

    span {
      display: inline-block;
      margin-left: 7px;
      color: #b6b6b6;
      font-size: 18px;
    }

    span,
    svg,
    path {
      transition: all $duration ease-in-out;
    }

    &:hover {
      svg {
        path {
          stroke: darken($color: #b6b6b6, $amount: 50);
        }
      }
      span {
        color: darken($color: #b6b6b6, $amount: 50);
      }
    }
  }

  .content__items {
    display: block;
  }

  &__item {
    display: flex;
    width: 100%;
    border-top: 1px solid $gray-line;
    border-bottom: 1px solid $gray-line;
    padding-top: 30px;
    padding-bottom: 30px;
    margin-top: 30px;

    &__left{
      display: flex;
      width: 60%;
      &-img { 
        margin-right: 10px;
        img {
          width: 110px;
          height: 90px;
        }
      }
  
      &-info {
        margin: 40px auto;
        display: flex;
        width: 80%;
  
        h3 {
          font-weight: bold;
          font-size: 22px;
        }
  
        @media (max-width: 800px) {
          h3{
            font-size: 18px;
          }
        }
      }

      @media (max-width: 925px) {
        &-info{
          margin-top: 30px;
        }

        &-info{
          margin-top: 30px;
        }

      }

      @media (max-width: 600px) {
        flex-direction: column;
        
        &-info{
          margin-left: -10px;
          margin-top: -10px;
          padding: 0 0 0 10px;
        }

      }
    }

    &-count {
      display: flex;
      align-items: center;
      justify-content: space-between;
      width: 13%;

      @media (max-width: 410px) {
        width: 50%;
      }

      &-minus {
        svg {
          path:first-of-type {
            display: none;
          }
        }

        &:disabled {
          opacity: 0.4;
          border-color: #787878;
          pointer-events: none;

          svg path{
            fill: #787878;
          }
        }
      }

      b {
        font-size: 22px;
      }
    }

    &-price {
      display: flex;
      align-items: center;
      justify-content: center;
      width: 43%;

      @media (max-width: 650px) {
        width: 43%;
      }

      @media (max-width: 505px) {
        width: 73%;
      }

      b {
        font-weight: bold;
        font-size: 22px;
        letter-spacing: 0.01em;

        @media (max-width: 780px) {
          font-size: 18px;
        }

        @media (max-width: 410px) {
          width: 50%;
        }
      }
    }

    &-remove {
      display: flex;
      align-items: center;
      justify-content: flex-end;
      width:4%;

      .button {
        border-color: darken($color: $gray-line, $amount: 10);
      }

      svg {
        transform: rotate(45deg);

        path {
          fill: darken($color: $gray-line, $amount: 15);
        }
      }

      .button {
        svg {
          width: 11.5px;
          height: 11.5px;
          position: relative;
        }
        &:hover,
        &:active {
          border-color: darken($color: $gray-line, $amount: 80);
          background-color: darken($color: $gray-line, $amount: 80);
        }
      }
    }
  }

  &__bottom {
    margin: 50px 0;

    &-details {
      display: flex;
      justify-content: space-between;

      span {
        font-size: 22px;

        &:last-of-type {
          b {
            color: $orange;
          }
        }
      }

      @media (max-width: 780px) {
        span{
          font-size: 18px;
        } 
      }

      @media (max-width: 450px) {
        span{
          width: 40%;
        }
      }
    }

    &-buttons {
      display: flex;
      justify-content: space-between;
      margin-top: 40px;

      .go-back-btn {
        display: flex;
        align-items: center;
        justify-content: center;
        width: 210px;

        border-color: darken($color: $gray-line, $amount: 10);

        span {
          color: darken($color: $gray-line, $amount: 20);
          font-weight: 500;
          font-weight: 600;
        }

        &:hover {
          background-color: darken($color: $gray-line, $amount: 90);
          border-color: darken($color: $gray-line, $amount: 90);

          span {
            color: $gray-line;
          }
        }

        svg {
          margin-right: 12px;
          path {
            fill: transparent;
            stroke-width: 2;
          }
        }
      }

      .pay-btn {
        font-size: 16px;
        font-weight: 600;
        width: 210px;
        padding: 16px;
      }
    }
  }
  
  &--empty {
    margin: 80px auto;
    width: 620px;
    text-align: center;

    @media (max-width: 720px) {
      width: 85%;
    }

    h2 {
      font-size: 36px;
      font-weight: 700;
      margin-bottom: 35px;
    }

    p {
      font-size: 20px;
      line-height: 145.4%;
      letter-spacing: 0.01em;
      color: #777777;
    }

    .cartImage{
      margin: 0 0 0 25px;
      img {
        display: block;
        width: 210px;
        margin: 45px auto 60px;
      }
    }
    

    .button--black {
      padding: 12px 0 14px;
      width: 230px;
      margin: 0 auto;
      font-weight: 600;
      font-size: 18px;
    }
  }
}

